.App {
  font-family: sans-serif;
  text-align: center;
}

.board {
  margin: 0 auto;
  padding: 5px;
}

.cell {
  float: left;
  width: 45px;
  height: 45px;
  line-height: 45px;
  text-align: center;
  cursor: pointer;
  border-radius: 7px;
  background: #ddf;
  color: black;
  border: none;
  margin: 3px;
}

.cell-dropdown {
  line-height: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  text-overflow: "";
}

.cell-special {
  background: #456;
  color: white;
}

.cell-role-start {
  background: #ada;
  color: white;
}

.cell-role-end {
  background: #daa;
  color: white;
}

.clear {
  clear: both;
  content: "";
}

.model-size-input {
}

.product-term {
  float: left;
  color: white;
  background: rgb(71, 188, 255);
  border: 1px solid rgb(55, 136, 184);
  border-radius: 10px;
  padding: 3px;
  margin: 2px;
}